import React from 'react'
import { Draggable } from 'react-beautiful-dnd'
import GripperVertical from '@elliemae/ds-icons/esm/GripperVertical'
import { getDraggableProps } from '../utils'

// eslint-disable-next-line react/display-name
const DraggableItem = renderers => (props) => {
  const {
    record,
    index,
    isItemFocused,
  } = props
  const ItemComponent = renderers.item
  return (
    <Draggable
      key={String(record.id)}
      draggableId={String(record.id)}
      index={index}
    >
      {(provided, snapshot) => (
        <ItemComponent
          {...getDraggableProps(props, provided, snapshot)}
          leftAddons={[
            <GripperVertical
              key='drag-handle'
              {...provided.dragHandleProps}
              className='drag-handle'
              tabIndex={isItemFocused ? 0 : -1}
            />,
            ...(props.leftAddons || []),
          ]}
        >
          {props.children}
        </ItemComponent>
      )}
    </Draggable>
  )
}

export default DraggableItem
