import { useEffect, useRef, useState } from 'react'
import useExpandState from '@elliemae/ds-utilities/hooks/useExpandState'
import { isFunction } from '@elliemae/ds-utilities/utils'
import { walkTreeStrategy } from './walkTreeStrategy'

const toggleExpandAllAsync = (expand, data) => new Promise((resolve) => {
  const nextState = {}
  if (expand) {
    walkTreeStrategy({
      data,
      cb: (node) => {
        if (node.isGroup) nextState[node.id] = true
      },
    })
  }
  resolve(nextState)
})

export const useExpandTreeState = (instance) => {
  const {
    originalData,
    props: {
      searchQuery,
      expandedRows: expandedItemsProp = {},
      itemKey = 'id',
      onExpand,
      onExpandChange,
      onExpandAll,
      onToggleExpand,
      onCollapse,
    },
  } = instance
  const expandedState = useExpandState({
    items: originalData,
    itemKey,
    expanded: expandedItemsProp,
    onExpand,
    onExpandChange,
    onExpandAll,
    onToggleExpand,
    onCollapse,
  })
  const totalExpandableRecords = useRef(0)
  const [expandedAll, setExpandAll] = useState(false)

  useEffect(() => {
    totalExpandableRecords.current = 0
    walkTreeStrategy({
      data: originalData,
      cb: (node) => {
        if (node.isGroup) totalExpandableRecords.current += 1
      },
    })
  }, [originalData])

  useEffect(() => {
    const currLength = Object.keys(expandedState.state.expanded || {}).length
    let isExpandedAll

    if (currLength === 0) isExpandedAll = false
    else if (totalExpandableRecords.current !== currLength) isExpandedAll = 'mixed'
    else if (currLength === totalExpandableRecords.current) isExpandedAll = true

    setExpandAll(isExpandedAll)
  }, [expandedState.state.expanded, originalData])


  const toggleExpand = (item, opts) => {
    const { expandAllChildren } = opts || {}
    expandedState.setExpanded((prevState) => {
      const nextExpandState = { ...prevState }
      const nextState = !prevState[item[itemKey]]

      if (nextState) nextExpandState[item[itemKey]] = true
      else delete nextExpandState[item[itemKey]]

      if (expandAllChildren) {
        walkTreeStrategy({
          data: item.children,
          cb: (node) => {
            if (node.isGroup) {
              if (nextState) nextExpandState[node.id] = true
              else delete nextExpandState[node.id]
            }
          },
        })
      }
      if (onExpandChange) {
        onExpandChange(nextExpandState, item)
      }
      return nextExpandState
    })
  }

  const handleToggleExpand = (item, opts) => {
    if (isFunction(onToggleExpand)) {
      onToggleExpand(item)
    } else {
      toggleExpand(item, opts)
    }
  }

  const toggleExpandAll = (expanded) => {
    let nextExpanded = true
    if (typeof expanded !== 'undefined') nextExpanded = expanded
    if (typeof expandedAll === 'boolean') nextExpanded = !expandedAll
    setExpandAll(nextExpanded)

    toggleExpandAllAsync(nextExpanded, originalData)
      .then(nextState => expandedState.setExpanded(nextState))
  }

  useEffect(() => {
    if (searchQuery && (!expandedAll || expandedAll === 'mixed')) toggleExpandAll(true)
  }, [searchQuery])

  return {
    state: { ...expandedState.state, expandedAll },
    actions: {
      ...expandedState.actions,
      toggleExpandAll,
      toggleExpand,
      _toggleExpand: handleToggleExpand,
    },
  }
}
