import React, { useMemo, useRef } from 'react'
import { omit } from 'lodash'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import { animated } from '@elliemae/ds-shared/react-spring'
import { useHiddenTransition } from '@elliemae/ds-basic/Hidden'
import { isFunction } from '@elliemae/ds-utilities/utils'
import { useOnClickOutside } from '@elliemae/ds-utilities/hooks'
import DSPortal from '@elliemae/ds-basic/Portal'


const blockName = 'popover'

function Popover({
  as = animated.div,
  destroyOnHidden = true,
  renderer = undefined,
  popperProps = {},
  visible = false,
  content = undefined,
  onClickOutside = () => null,
  style,
  ...otherProps
}) {
  const popoverRef = useRef()
  const hiddenTransitionProps = useHiddenTransition({ visible })
  const PopoverComponent = useMemo(() => aggregatedClasses(as)(blockName), [as])
  const popoverProps = {
    as,
    content,
    ref: popoverRef,
    ...otherProps,
    ...hiddenTransitionProps,
  }

  useOnClickOutside(popoverRef, onClickOutside)

  const wrapStyle = omit(style, 'zIndex') || {}
  return destroyOnHidden && !hiddenTransitionProps.destroyed && (
    <DSPortal>
      <div
        {...popperProps}
        style={{ ...style, ...popperProps.style }}
      >
        {isFunction(renderer)
          ? renderer(popoverProps)
          : (
            <PopoverComponent {...popoverProps} style={wrapStyle} />
          )
      }
      </div>
    </DSPortal>
  )
}

export default Popover
