const defaultStrategy = () => {
  throw Error('Implement an iterator strategy for record iterator')
}

export function recordIterator(records, strategy = defaultStrategy, decorators = [], instance) {
  const nextRows = []

  strategy({
    data: records,
    cb: (row) => {
      let nextRow = row

      // eslint-disable-next-line no-loops/no-loops,no-plusplus
      for (let i = 0; i < decorators.length; i++) {
        nextRow = decorators[i](nextRow, instance)
        if (!nextRow) break
      }

      if (nextRow) nextRows.push(nextRow)
    },
    instance,
  })

  return nextRows
}
