import { FluidHeightList } from './FluidHeightList'
import { AutoHeightList } from './AutoHeightList'

export {
  FluidHeightList,
  AutoHeightList,
}

export function getVirtualListComponent({
  component: Component,
  fluid,
  height,
  autoHeight,
}) {
  if (autoHeight) return AutoHeightList
  if (fluid) return FluidHeightList
  return Component
}
