import React, {
  useMemo, useRef, useState, useEffect,
} from 'react'
import DSPopover from '@elliemae/ds-basic/Popover'
import { useOnClickOutside } from '@elliemae/ds-utilities/hooks'

export const ToolbarContext = React.createContext()

const { Provider } = ToolbarContext

const useScrollListener = (callback, dependencies) => {
  useEffect(() => {
    window.addEventListener('scroll', callback, true)
    return () => {
      window.removeEventListener('scroll', callback, true)
    }
  }, [dependencies])
}

const noop = () => null
function ToolbarProvider({
  delay = 200,
  children,
  onHide = noop,
  onShow = noop,
}) {
  const showTimer = useRef()
  const hideTimer = useRef()
  const toolbarRef = useRef()
  const [tooltipState, setTooltipState] = useState({})

  const show = (state) => {
    if (state.reference === tooltipState.reference) clearTimeout(hideTimer.current)
    showTimer.current = setTimeout(
      () => setTooltipState({
        ...state,
        visible: true,
      }),
      delay,
    )
    onShow()
  }

  const hide = (e, force) => {
    if (!force) clearTimeout(showTimer.current)
    hideTimer.current = setTimeout(
      () => setTooltipState(prevState => ({
        ...prevState,
        visible: false,
      })),
      100,
    )
    onHide(tooltipState, e)
  }

  useScrollListener(e => hide(e, true), tooltipState.visible)
  useOnClickOutside(toolbarRef, () => hide())

  const keyDownHandler = (e) => {
    if (e.key === 'Escape') {
      hide(e)
    }
  }

  useEffect(() => {
    window.addEventListener('keydown', keyDownHandler)
    return () => {
      window.removeEventListener('keydown', keyDownHandler)
    }
  })

  const value = useMemo(() => {
    return {
      state: tooltipState,
      show,
      hide,
    }
  }, [tooltipState.reference])

  return (
    <Provider value={value}>
      {children}
      <DSPopover
        boundaries='scrollParent'
        {...(tooltipState.options || {})}
        content={tooltipState.renderer}
        placement='left'
        referenceEl={tooltipState.reference}
        renderer={({ as: Component, style, children: Content }) => {
          return (
            <div
              ref={toolbarRef}
              onMouseEnter={e => show(tooltipState, e)}
              onMouseLeave={hide}
            >
              <Component
                style={style}
              >
                {Content}
              </Component>
            </div>
          )
        }}
        style={tooltipState.style}
        visible={tooltipState.visible}
      />
    </Provider>
  )
}

export default React.memo(ToolbarProvider)
