import React, { forwardRef, useRef } from 'react'
import { FixedSizeList as List } from 'react-window'
import { cx } from '@elliemae/ds-utilities/utils'
import { mergeRefs } from '@elliemae/ds-utilities/system'
import useWindowScrollerList from '@elliemae/ds-utilities/hooks/useWindowScrollerList'

export const AutoHeightList = forwardRef(({
  itemCount,
  className,
  innerRef,
  itemData,
  itemKey,
  itemSize,
  outerRef,
  component: ListComponent = List,
  ...otherProps
}, ref,
) => {
  const listRef = useRef()
  const outerListRef = useRef()

  useWindowScrollerList({
    listInstance: listRef,
    outerListRef,
  })

  return (
    <ListComponent
      {...otherProps}
      ref={mergeRefs(ref, listRef)}
      className={cx(className, 'window-scroller-override')}
      height={window.innerHeight}
      innerRef={innerRef}
      itemCount={itemCount}
      itemData={itemData}
      itemKey={itemKey}
      itemSize={itemSize}
      outerRef={mergeRefs(outerListRef, outerRef)}
      overscanCount={10}
      useIsScrolling
    />
  )
})
