import get from 'lodash/get'

export const getParentId = record => get(record, ['parentNode', 'id'])
export const getParentChildren = (record, childrenKey = 'children') => get(record, ['parentNode', 'data', childrenKey])
export const getItemLabel = (record, nameKey = 'name') => get(record, ['data', nameKey])
export const getItemChildren = (record, childrenKey = 'children') => get(record, ['data', childrenKey])
export const getItemChildrenLength = (record, childrenKey = 'children') => {
  const children = get(record, ['data', childrenKey])
  return children && children.length
}
export const findItemById = (records, id) => records.find(record => String(record.id) === id)
