import React, { useContext, useMemo, useRef } from 'react';
import MoreOptionsVert from '@elliemae/ds-icons/MoreOptionsVert';
import DSButton from '@elliemae/ds-basic/Button';
import { runAll } from '@elliemae/ds-utilities/utils';
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin';
import ToolbarProvider, {
  ToolbarContext,
} from '@elliemae/ds-shared/toolbar/ToolbarProvider';
import FocusGroupContext from '@elliemae/ds-shared/FocusGroup/FocusGroupContext';

const ToolbarDisclosure = props => {
  const {
    onMouseEnter,
    onMouseLeave,
    extraParams,
    renderToolbar,
    isItemFocused,
    icon = <MoreOptionsVert color={['brand-primary', 800]} />,
    rowSize,
    ...rest
  } = props;

  const toolbarContext = useContext(ToolbarContext);
  const ref = useRef(null);
  const show = () => {
    if (ref.current) {
      const toolbar = renderToolbar({
        onCloseToolbar: toolbarContext.hide,
        ...extraParams,
        size: rowSize,
      });
      toolbarContext.show({
        reference: ref.current,
        renderer: toolbar,
      });
    }
  };

  const hide = e => {
    toolbarContext.hide(e);
  };

  return (
    <DSButton
      {...rest}
      buttonType="text"
      className="toolbar-trigger"
      icon={icon}
      innerRef={ref}
      onClick={show}
      onMouseEnter={runAll(show, onMouseEnter)}
      onMouseLeave={runAll(hide, onMouseLeave)}
      tabIndex={isItemFocused ? 0 : -1}
    />
  );
};

const TriggerFormatter = (value, extraParams, instance) => {
  const {
    props: { onShowToolbar, onCloseToolbar, renderToolbar, rowSize },
  } = instance.getInstance();

  return (
    <>
      {value}
      <ToolbarDisclosure
        isItemFocused={extraParams.isItemFocused}
        onClose={onCloseToolbar}
        onShow={onShowToolbar}
        renderToolbar={renderToolbar}
        rowSize={rowSize}
      />
    </>
  );
};

export const TreeToolbarPlugin = createInstancePlugin('tree-toolbar', {
  itemRenderer: TriggerFormatter,
  renderers(renderers) {
    const List = useMemo(() => {
      const Component = renderers.list;
      // eslint-disable-next-line react/display-name
      return props => {
        const focusContext = useContext(FocusGroupContext);
        return (
          <ToolbarProvider
            onHide={(state, e) => {
              if (e && e.type === 'keydown' && focusContext)
                focusContext.focus();
            }}
          >
            <Component {...props} />
          </ToolbarProvider>
        );
      };
    }, []);
    return {
      ...renderers,
      list: List,
    };
  },
});
