import React from 'react'
import PropTypes from 'prop-types'
import { useHiddenController } from '@elliemae/ds-basic/Hidden'
import useHoverHandlersDelay from '@elliemae/ds-utilities/hooks/useHoverHandlersDelay'
import Popover from './Popover'
import usePopper from './usePopper'
import Arrow from './Arrow'
import usePopoverProviderState from './usePopoverProviderState'

export * from '@elliemae/ds-basic/Popper'
export const Interaction = {
  CLICK: 'click',
  HOVER: 'hover',
}

/* Unstable Popover, not yet fully working */
function DSPopover({
  containerProps = {},
  content, // react component inside the popover to render its content
  renderer = undefined, // function to render the popover component, ex: (popoverProps) => (<div {...popoverProps}></div>)
  visible: visibleProp = undefined,
  offset = undefined,
  showArrow = false,
  // popper props options
  placement = 'top',
  boundaries, // scrollParent | window | viewport
  modifiers = {},
  referenceEl,
  renderTrigger = () => null,
  onClickOutside = () => null,
  destroyOnHidden = true,
  interactionType,
  isOpen = undefined,
  style,
}) {
  const {
    visible, onShow, onHide, onToggle,
  } = useHiddenController(visibleProp)

  const {
    reference, popper, arrow, popperData,
  } = usePopper({
    placement,
    showArrow,
    boundaries,
    modifiers,
    offset,
    referenceEl,
    eventsEnabled: visible,
    isOpen,
  })

  const hoverHandlers = useHoverHandlersDelay({
    onMouseOver: onShow,
    onMouseLeave: onHide,
  })
  return (
    <>
      {renderTrigger({
        ...reference,
        handlers: hoverHandlers,
        onShow,
        onToggle,
        onHide,
        'aria-haspopup': true,
        'aria-expanded': visible,
      })}
      {reference.ref && (
        <Popover
          containerProps={containerProps}
          destroyOnHidden={destroyOnHidden}
          interactionType={interactionType}
          isOpen={isOpen}
          onClickOutside={onClickOutside}
          popperProps={popper}
          renderer={
            renderer
            && (props => renderer({
              ...props,
              onShow,
              onToggle,
              onHide,
            }))
          }
          style={style}
          visible={visible || isOpen}
        >
          {showArrow && <Arrow {...arrow} placement={popperData.placement} />}
          {(visible || isOpen) && content}
        </Popover>
      )}
    </>
  )
}

DSPopover.propTypes = {
  containerProps: PropTypes.shape({}),
  content: PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
  renderer: PropTypes.func,
  visible: PropTypes.bool,
  interaction: PropTypes.string, // todo: add proptypes from proptypes utils to get enum
  offset: PropTypes.shape({}),
  showArrow: PropTypes.bool,
  placement: PropTypes.string, // todo: add proptypes from proptypes utils to get enum
  boundaries: PropTypes.string, // todo: add proptypes from proptypes utils to get enum
  modifiers: PropTypes.shape({}),
  // referenceEl: PropTypes.func,
  // renderTrigger: PropTypes.func,
  // destroyOnHidden: PropTypes.func,
}

export { usePopper, usePopoverProviderState }

export default DSPopover
