import {
  useCallback, useMemo, useState,
} from 'react'
import { isEqual } from '@elliemae/ds-utilities/utils'
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin'
import DraggableItem from './renderers/DraggableItem'
import DroppableList from './renderers/DroppableList'
import {
  changePositionBetweenDifferentArrays,
  changePositionSameArray,
  getTreePosition,
} from './utils'

export const TreeDndPlugin = createInstancePlugin('tree-dnd', {
  state(instance) {
    const {
      originalData,
      props: {
        childrenKey = 'children',
        onOrderChange = () => null,
      },
    } = instance

    const [draggedRecord, setDraggedRecord] = useState(null)

    const changeRecordsPosition = useCallback(({
      sourceRecord, targetRecord, combineWith, down,
    }) => {
      let nextTreeData

      const sourceTreePosition = getTreePosition(sourceRecord.path)
      const targetTreePosition = combineWith
        ? { parentPath: combineWith.path }
        : getTreePosition(targetRecord.path)

      if (!combineWith && isEqual(sourceTreePosition.parentPath, targetTreePosition.parentPath)) {
        nextTreeData = changePositionSameArray({
          sourceRecord,
          targetRecord,
          sourceTreePosition,
          targetTreePosition,
          data: originalData,
        })
      } else {
        nextTreeData = changePositionBetweenDifferentArrays({
          sourceRecord,
          targetRecord: combineWith || targetRecord,
          combineWith,
          sourceTreePosition,
          targetTreePosition,
          data: originalData,
          down,
        })
      }

      onOrderChange(nextTreeData)

      return nextTreeData
    }, [originalData, childrenKey])

    return {
      state: { draggedRecord },
      actions: { setDraggedRecord, changeRecordsPosition },
    }
  },
  renderers(renderers, instance) {
    const item = useMemo(() => DraggableItem(renderers, instance), [])
    const list = useMemo(() => DroppableList(renderers, instance), [])
    return { ...renderers, item, list }
  },
})
