import { useMemo } from 'react'
import { setRef } from '@elliemae/ds-utilities/system'
import get from 'lodash/get'
import { composeFormatters, reducePropsGetter } from '../createDataInstance/utils'
import { recordIterator } from './recordIterator'
import createInstanceRef from '../createDataInstance/createInstanceRef'

export const getItemLabel = (record, nameKey = 'name') => get(record, ['data', nameKey])
export const getItemChildren = (record, childrenKey = 'children') => get(record, ['data', childrenKey])

const listRenderers = {
  list: 'ul',
  item: 'li',
}

// todo: check if a plugin is a LIST plugin rather than GRID
export default function useDataList({
  data = [],
  plugins = [],
  renderers = listRenderers,
  itemRenderer = [],
  customDecorators,
  ...props
}) {
  const { instance, decorators } = createInstanceRef({
    data,
    renderers,
    plugins,
    customDecorators: {
      ...customDecorators,
      recordIterator,
      getListProps: [],
      getItemProps: [],
      itemRenderer: [...itemRenderer], // components reduce to compose item content
      record: [],
      records: [],
    },
    ...props,
  })
  instance.current.getItemLabel = record => getItemLabel(record, props.nameKey)
  instance.current.getItemChildren = record => getItemChildren(record, props.childrenKey)

  instance.current.decorators = decorators
  instance.current.getListProps = reducePropsGetter(decorators.getListProps, instance.current)
  instance.current.getItemProps = reducePropsGetter(decorators.getItemProps, instance.current)
  instance.current.itemRenderer = composeFormatters(instance.current.decorators.itemRenderer, instance.current)

  instance.current.records = useMemo(() => {
    // record iterator TREE / Simple
    let nextRecords = recordIterator(
      data,
      instance.current.decorators.recordIterator,
      decorators.record,
      instance.current,
    )

    // compose all records
    nextRecords = decorators.records
      .reduce((nextRows, getter) => {
        return getter(nextRows, instance.current)
      }, nextRecords)

    return nextRecords
  })

  if (props.instanceRef) setRef(props.instanceRef, instance.current)

  return instance.current
}
